/* Navbar container */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2c3e50;
    padding: 10px 20px;
    color: #fff;
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  /* Logo */
  .navbar-logo .logo {
    width: 50px;
  }
  
  /* Navigation links */
  .navbar-links {
    list-style: none;
    display: flex;
    gap: 20px;
  }
  
  .navbar-links li a {
    text-decoration: none;
    color: #fff;
    font-size: 16px;
    transition: color 0.3s;
  }
  
  .navbar-links li a:hover {
    color: #f39c12;
  }
  
  /* Search bar */
  .navbar-search {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  .search-input {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .search-button {
    padding: 5px 10px;
    background-color: #f39c12;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s;
  }
  
  .search-button:hover {
    background-color: #e67e22;
  }
  
  /* Profile or Cart Icon */
  .navbar-icons .icon {
    width: 50px;
    border-radius: 20%;
    cursor: pointer;
  }
  